.jumbotron {
  padding: 25px;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: bisque;
  border-bottom: 8px solid orange;
  border-top: 8px solid orange;
  font-family: 'Acme', sans-serif;
  font-size: 25px;
  color: orangered;
}

.title {
  font-family: 'Acme', sans-serif;
  font-size: 25px;
  color: #a54403;
}
.card-header {
  background-color: bisque;
}
.list-group-item p {
  font-size: 16px;
}

.h3font {
  font-family: 'Acme', sans-serif;
  font-size: 25px;
  color: #a54403;
}
.pstyle {
  font-family: 'Cabin', sans-serif;
  font-size: 15px;
  text-align: justify;
}

.btn-container {
  float: right;
}

@media (max-width: 767px) {
  .btn-container {
    margin-bottom: 15px;
  }
}

.fa-react {
  font-size: 15px;
  color: blue;
}
.navbar {
  border-radius: 0;
}

.navbar-brand {
  font-family: 'Acme', sans-serif;
  font-size: 25px;
  color: orangered;
}
.nav-item {
  font-family: 'Cabin', sans-serif;
  font-size: 20px;
}

